    .header {
        width: 100%;
        position: fixed;
        top: 0px;
        left: 0px;
        z-index: var(--z-fixed);
        background-color: var(--body-color);
    }
    
    .nav {
        height: calc(var(--header-height) + 1.5rem);
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 1rem;
    }
    
    .nav___logo,
    .nav___toggle {
        color: var(--title-color);
        font-weight: var(--font-medium);
    }
    
    .nav___list {
        display: flex;
        column-gap: 2rem;
    }
    
    .nav___icon,
    .nav___close,
    .nav___toggle {
        display: none;
    }
    
    .nav___link {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: var(--medium-font-size);
        color: var(--title-color);
        font-weight: var(--font-medium);
        transition: .3s;
    }
    /* Active link */
    
    .active-link,
    .nav___link:hover {
        color: var(--title-color-dark);
    }
    /*=============== BREAKPOINTS ===============*/
    /* For medium devices */
    
    .logo {
        font-size: 3rem;
        font-family: 'Chela One', cursive;
    }
    
    @media screen and (max-width: 768px) {
        .header {
            top: initial;
            bottom: 0;
        }
        .nav {
            height: var(--header-height);
        }
        .nav___menu {
            position: fixed;
            bottom: -100%;
            left: 0;
            width: 100%;
            background-color: var(--body-color);
            padding: 2rem 1.5rem 4rem;
            box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
            border-radius: 1.5rem 1.5rem 0 0;
            transition: .3s;
        }
        /* Show Menu */
        .show-menu {
            bottom: 0;
        }
        .nav___list {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
        }
        .nav___icon {
            font-size: 1.2rem;
        }
        .nav___close {
            position: absolute;
            right: 1.3rem;
            bottom: .5rem;
            font-size: 1.5rem;
            cursor: pointer;
            color: var(--title-color);
        }
        .nav___close:hover {
            color: var(--title-color-dark);
        }
        .nav___toggle {
            font-size: 1.7rem;
            cursor: pointer;
        }
        .nav___icon,
        .nav___close,
        .nav___toggle {
            display: block;
        }
    }
    /* For small devices */
    
    @media screen and (max-width: 350px) {
        .nav___menu {
            padding: 2rem 0.25rem 4rem;
        }
        .nav___list {
            column-gap: 0;
        }
    }